import React from 'react'
import Link from 'gatsby-link'
import District from '../../components/District'

const CuChi = ({ data }) => {
  return <District nodes={data.allLocationsXlsxSheet1.edges} />
}

export default CuChi

export const CuChiQuery = graphql`
  query CuChiQuery {
    allLocationsXlsxSheet1(filter: { district: { eq: "Cá»§ Chi" } }) {
      edges {
        node {
          plf_id
          complete_address
          longitude
          latitude
          city
          ward
          district
          planned_launch_date
          week
          status
          contact_email
          notes
        }
      }
    }
  }
`



